@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,300;0,400;0,500;0,600;0,700;1,200;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.about-me,
.projects,
.experience {
  padding-bottom: 100px;
  min-height: 100vh;
}

html {
  background-color: black;
  font-family: "IBM Plex Mono", monospace;
  min-width: 530px;
}

.App {
  text-align: center;
}

.homepage {
  display: flex;
  flex-direction: column;
  color: white;
  justify-content: center;
  align-items: center;
  /* horizontally */
  width: 100%;
  margin: auto;
  /* vertically */
  position: absolute;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  font-family: "IBM Plex Mono", monospace;
}

.home-introduce {
  font-size: 300%;
  font-weight: bold;
}

.home-img {
  border-radius: 50%;
  background-color: white;
  margin: 30px;
  height: 30%;
  width: 30%;
}

.home-text {
  font-size: 60%;
  color: white;
}

.home-text:hover {
  cursor: pointer;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-family: "IBM Plex Mono", monospace;
}

.home-icon {
  border-radius: 50%;
  background-color: white;
  margin: 10px;
  height: 70px;
  width: 70px;
}

.nav-bar {
  color: white;
}

.nav-bar ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.nav-bar a {
  margin: 10px;
  padding: 10px;
  font-weight: bold;
  color: white;
  text-decoration: none;
}

.nav-bar a.active {
  color: #7cfcda;
  text-decoration: underline;
}

.nav-bar a:hover {
  color: white;
  background-color: #40af92;
  transition-duration: 0.5s;
}

.nav-bar a.active:hover {
  color: #7cfcda;
  background: none;
}

.footer {
  background-color: #7cfcda;
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.footer-icon {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-icon img {
  height: 60px;
  width: 60px;
}

.sns {
  display: flex;
  justify-content: center;
  width: 100px;
  align-items: center;
}

.sns a {
  color: #114a3b;
  font-size: larger;
  padding: 5px;
}

.sns a:hover {
  border-radius: 5px;
  color: white;
  background-color: #114a3b;
  transition-duration: 0.5s;
}

.about-me {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10%;
  height: 100%;
}
.about-icon {
  background-color: #7cfcda;
  border-color: #7cfcda;
  border-radius: 50%;
  height: 200px;
  width: 200px;
  padding: 20px;
}
.about-text {
  color: white;
  font-family: "IBM Plex Mono", monospace;
  text-align: center;
  width: 50%;
  margin-top: 50px;
  font-weight: bold;
}
.scroll {
  margin: 10%;
  -webkit-animation: bounce 2s ease infinite;
          animation: bounce 2s ease infinite;
}
@-webkit-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
}
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
}
.stats {
  height: 50%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px;
  gap: 30px;
}

.stat-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.stat-item text {
  color: white;
  font-family: "IBM Plex Mono", monospace;
}

.card {
  margin: 10px;
}

.card-content {
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

a {
  text-decoration: none;
  color: black;
}

.links {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.projects-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow-x: auto;
  position: relative;
  margin: 50px;
}

.projects h2 {
  color: #7cfcda;
  font-family: "Amiko", sans-serif;
  font-family: "IBM Plex Mono", monospace;
  font-weight: 800;
  font-style: italic;
  margin-left: 50px;
}

.experience {
  color: #7cfcda;
  margin: 50px;
}

.experience h2 {
  font-family: "Amiko", sans-serif;
  font-family: "IBM Plex Mono", monospace;
  font-weight: 800;
  font-style: italic;
  margin-left: 50px;
}

.experience-content {
  margin-top: 100px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.left-box {
  display: flex;
  flex-direction: row;
  margin-right: -10px;
  padding-right: 0;
  max-width: 450px;
}

.right-box {
  display: flex;
  flex-direction: row;
  margin-left: -10px;
  max-width: 450px;
}

.line {
  width: 50px;
  height: 2px;
  border: 1px solid #7cfcda;
  background-color: #7cfcda;
  border-radius: 20px;
  position: relative;
  top: 25px;
  margin: 0px;
}

.experience-left {
  margin-right: 0px;
}

.experience-right {
  margin-left: 0px;
  margin-top: 100px;
}

.timeline {
  width: 25px;
  flex-shrink: 0;
  min-height: 100vh;
  background-color: #7cfcda;
  border: 1px solid #7cfcda;
  border-radius: 20px;
  margin: 0;
}

.experience-item-left,
.experience-item-right {
  margin: 20px;
  margin-bottom: 100px;
  display: inline-block;
  max-width: 450px;
}

.experience-item-left {
  text-align: end;
}

.position {
  font-family: "IBM Plex Mono", monospace;
  font-weight: 700;
  margin-bottom: 10px;
  display: inline-block;
  max-width: 450px;
}

.duration {
  font-family: "IBM Plex Mono", monospace;
  font-weight: 200;
  font-style: italic;
  color: white;
  max-width: 400px;
}

.description {
  text-align: left;
  list-style-type: "✧ ";
  color: white;
  max-width: 400px;
}

.experience-item-left .description {
  float: right;
}

